.contact {
  background-color: #0d2e620d;

  &__text {
    max-width: 1000px;
    margin-bottom: var(--mb-3);
  }

  &__row {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-3);
  }

  &__form {
    &-group {
      &:nth-last-of-type(1) {
        grid-column: span 2;
      }
    }

    form {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: var(--mb-0-75);
    }

    label {
      display: block;
      font-weight: bold;
      margin-bottom: 10px;
      color: var(--black-color);
    }

    input {
      width: 100%;
      padding: 15px;
      border-radius: 4px;
      margin-bottom: 15px;
      border: 1px solid #ccc;
      border-radius: 4px;
      font-size: 16px;

      &:focus {
        border-color: #007BFF;
        outline: none;
      }
    }

    textarea {

      width: 100%;
      min-height: 250px;
      resize: none;
      padding: 15px;
      border-radius: 4px;
      margin-bottom: 15px;
      border: 1px solid #ccc;
      border-radius: 4px;
      font-size: 16px;

      &:focus {
        border-color: #007BFF;
        outline: none;
      }
    }

  }

  &__btn {
    grid-column: span 2;
    justify-content: center;
    width: 100%;
    text-align: center;
  }

  &__list {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-1);

    &:not(:last-child) {
      border-bottom: 1px solid var(--black-color);
      margin-bottom: var(--mb-1);
      padding-bottom: var(--mb-1);
    }
  }

  &__subtitle {
    margin-bottom: var(--mb-1);
    font-size: clamp(1.375rem, 1.339rem + 0.18vw, 1.5rem);
    line-height: var(--line-height);
  }

  &__item {
    border-bottom: 1px solid #cecece;
    padding-bottom: var(--mb-1);

    &-title {
      margin-bottom: var(--mb-0-5);
      font-size: clamp(1.125rem, 1.089rem + 0.18vw, 1.25rem);
      line-height: var(--line-height);
    }

    &-name {
      margin-bottom: var(--mb-0-5);
      font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
      line-height: var(--line-height);
    }

    &-link {
      display: inline-block;
      margin-bottom: var(--mb-0-5);
      font-weight: var(--semibold-font);
      color: var(--blue-color);
      transition: color .2s ease-in-out;

      &:focus {
        outline: none;
      }

      @include hover {
        color: var(--red-color);

      }

      &:active {
        color: var(--black-color);
      }
    }
  }
}

.accordion__item {
  margin-bottom: var(--mb-1);
}

.accordion__header {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: none;
  border-radius: var(--border-radius);
  width: 100%;
  padding: var(--mb-1) var(--mb-2);
  font-size: clamp(1.125rem, 1.089rem + 0.18vw, 1.25rem);
  font-weight: var(--semibold-font);
  text-align: left;
  background-color: var(--white-color);
  transition: background-color 0.3s ease;
}

.accordion__header:hover {
  background: #eaeaea;
}

.accordion__icon {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  border-right: 2px solid #333;
  border-bottom: 2px solid #333;
  transform: rotate(45deg);
  transition: transform 0.3s ease;
}

.accordion__item.is-active .accordion__icon {
  transform: rotate(-135deg);
}

.accordion__body {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.4s ease, padding 0.4s ease;
  padding: 0 1.5rem;
}

.accordion__item.is-active .accordion__body {
  padding-top: var(--mb-1);
  padding-bottom: var;
  max-height: 1000px;
}

@media screen and (max-width:1000px) {
  .contact {
    &__row {
      display: flex;
      flex-direction: column-reverse;
    }
  }
}

@media screen and (max-width:600px) {
  .contact {
    &__list {
      grid-template-columns: 1fr;
    }
  }
}

@media screen and (max-width:425px) {
  .contact {
    &__form {
      form {
        display: flex;
        flex-direction: column;
      }
    }
  }
}
