.hero {
  position: relative;
  padding: 50px 0;
  min-height: 700px;
  height: 700px;

  &__container {
    height: 100%;
  }

  &__video {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__info {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1100px;
    height: 100%;
    color: var(--white-color);

    h2 {
      margin-bottom: var(--mb-3);
      font-size: clamp(1.875rem, 1.518rem + 1.79vw, 3.125rem);
      line-height: var(--line-height);
    }

    p {
      margin-bottom: 50px;
      max-width: 80%;
      font-size: clamp(1.125rem, 1.054rem + 0.36vw, 1.375rem);
      font-weight: 500;
      line-height: var(--line-height);
    }
  }

  &__list {
    gap: var(--mb-1);
  }

  &__btn {
    display: block;
    position: relative;
    overflow: hidden;
    border: 2px solid transparent;
    border-radius: var(--border-radius);
    width: 250px;
    padding: var(--mb-1);
    color: var(--white-color);
    text-align: center;
    line-height: var(--line-height);
    background-color: var(--orange-color);
    transition: all .2s ease-in-out;

    span {
      position: relative;
      z-index: 2;
      font-size: clamp(1rem, 0.929rem + 0.36vw, 1.25rem);
      font-weight: var(--semibold-font);
      color: var(--blue-color);
      transition: color .2s ease-in-out;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: var(--white-color);
      transform: translateX(-200%);
      transition: transform .2s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      border: 2px solid var(--white-color);

      span {
        color: var(--dark-color);
      }

      &::before {
        transform: translateX(0);
      }
    }

    &:active {
      border-color: var(--white-color);
      background-color: transparent;

      span {
        color: var(--white-color);
      }

      &::before {
        transform: translateX(100%);
      }
    }

    &--white {
      background-color: var(--white-color);

      span {
        color: var(--blue-color);
      }

      &:focus {
        outline: none;
      }

      @include hover {
        border: 2px solid var(--dark-blue-color);

        span {
          color: var(--white-color);
        }

        &::before {
          background-color: var(--dark-blue-color);
          transform: translateX(0);
        }
      }

      &:active {
        border-color: var(--white-color);
        background-color: transparent;

        span {
          color: var(--white-color);
        }

        &::before {
          transform: translateX(100%);
        }
      }
    }
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: linear-gradient(to top right, #0d2e623f 0%, #071a3a76 70%, transparent 100%);
  }
}


@media screen and (max-width:600px) {
  .hero {
    &__info {
      h2 {
        text-align: center;
      }

      p {
        max-width: 100%;
        font-weight: var(--regular-font);
        text-align: center;
      }
    }

    &__list {
      flex-direction: column;
    }

    &__btn {
      margin: auto;
      width: 90%;
    }
  }
}


@media screen and (max-width:375px) {
  .hero {
    min-height: 100vh;
    height: 100vh;
    padding: 100px 0 50px;
  }
}
