.magazine {
  position: relative;
  background-image: url("../img/magazine-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;

  &__info {
    position: relative;
    z-index: 2;
  }

  &__title {
    border-left: none;
    padding-left: 0;
    color: var(--white-color);
    text-align: center;
  }

  &__subtitle {
    margin-bottom: var(--mb-3);
    color: var(--white-color);
    text-align: center;
  }

  &__text {
    margin: 0 auto var(--mb-3);
    max-width: 1000px;
    color: var(--white-color);
    text-align: center;
  }

  &__iframe {
    margin:0 auto var(--mb-3);
    max-width: 1000px;
    height: 700px;

    iframe {
      display: block;
      width: 100%;
      height: 100%;
      border: transparent;
    }
  }

  &__btn {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: #0000008a;
  }
}

.btn {
  &__list {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-1);
    margin: auto;
    max-width: 800px;
  }
}

@media screen and (max-width:768px) {
  .magazine {
    &__iframe {
      height: 500px;
    }
  }
  .btn {
    &__list {
      grid-template-columns: 1fr;
    }
  }
}
