.features {
  &__list {
    position: relative;
    z-index: 3;
    grid-template-columns: repeat(3, 1fr);
    border-radius: var(--border-radius);
    margin-top: -130px;
    box-shadow: 0 0 4px 1px var(--blue-color);
    background-color: var(--light-color);
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--mb-1);
    padding: var(--mb-4) var(--mb-1);
    text-align: center;

    &:not(:last-child) {
      border-right: 1px solid #cecece;
    }
  }

  &__title {
    font-size: clamp(1.25rem, 1.214rem + 0.18vw, 1.375rem);
    font-weight: var(--semibold-font);
    line-height: var(--line-height);
  }

  &__text {
    line-height: var(--line-height);
  }

  &__icon {
    width: 80px;
    height: 80px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: var(--blue-color);
    }
  }
}

@media screen and (max-width:600px) {
  .features {
    &__list {
      grid-template-columns: 1fr;
    }
    &__item {
      &:not(:last-child) {
        border-right: none;
        border-bottom: 1px solid #cecece;
      }
    }
    &__icon {
      width: 60px;
      height: 60px;
    }
  }
}

@media screen and (max-width:400px) {
  .features {
    &__list {
      margin-top: -50px;
    }
  }
}


@media screen and (max-width:375px) {
  .features {
    padding-top: 50px;
    &__list {
      margin-top: 0;
    }
  }
}
