.editorial {
  background-color: #0d2e620d;

  &__container {
    gap: var(--mb-3);
  }

  &__info {
    width: 70%;
  }

  &__text {
    margin-bottom: var(--mb-3);
  }

  &__list {
    margin-bottom: var(--mb-3);
    padding-left: var(--mb-3);
    list-style: square;

    &-item {
      font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
      font-weight: var(--semibold-font);
      text-transform: uppercase;
      line-height: var(--line-height);

      &:not(:last-child) {
        margin-bottom: var(--mb-1);
      }
    }
  }

  &__box {
    gap: var(--mb-1);

    &-image {
      width: 300px;
      height: 300px;

      img {
        display: block;
        border-radius: var(--border-radius);
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &-info {
      flex: 1;
    }

    &-text {
      margin-bottom: var(--mb-2);
    }

    &-title {
      margin-bottom: var(--mb-1);
      font-size: clamp(1.25rem, 1.143rem + 0.54vw, 1.625rem);
      line-height: var(--line-height);
    }

    &-link {
      position: relative;
      overflow: hidden;
      display: inline-block;
      margin-bottom: var(--mb-0-75);
      font-size: 18px;
      font-weight: var(--semibold);
      padding-bottom: 5px;
      color: var(--blue-color);
      transition: color .2s ease-in-out;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 70%;
        height: 2px;
        background-color: var(--red-color);
        transform: translateX(-200%);
        transition: transform .2s ease-in-out;
      }

      &:hover {
        color: var(--red-color);

        &::before {
          transform: translateX(0);
        }
      }
    }

    &-signature {
      width: 220px;
    }
  }

  &__image {
    position: relative;
    width: 30%;

    &-text {
      // position: absolute;
      // left: 0;
      // bottom: 50px;
      border-left: 4px solid var(--red-color);
      padding: var(--mb-3) var(--mb-1);
      background-color: var(--white-color);

      h3 {
        margin-bottom: var(--mb-1);
        font-size: clamp(1.125rem, 1.054rem + 0.36vw, 1.375rem);
        line-height: var(--line-height);
      }
    }
  }
}

@media screen and (max-width:1000px) {
  .editorial {
    &__container {
      flex-direction: column;
    }

    &__info,
    &__image {
      width: 100%;
    }

    &__image {
      img {
        display: block;
        margin: auto;
        max-width: 500px;
      }
    }
  }
}

@media screen and (max-width:767px) {
  .editorial {
    &__box {
      flex-direction: column;
      align-items: center;
    }
  }
}

@media screen and (max-width:500px) {
  .editorial {
    &__box {
      &-image {
        width: 100%;
        height: 100%;
      }
    }

    &__image {
      img {
        max-width: 100%;
      }
    }
  }
}
