.burger {
  position: relative;
  z-index: 1000;
  display: none;
  padding: 0;
  width: 32px;
  height: 20px;
  cursor: pointer;
}
.burger__line {
  position: absolute;
  right: 0;
  top: 50%;
  width: 100%;
  height: 2px;
  background-color: var(--black-color);
  transform: translateY(-50%);
  transition: opacity 0.2s ease-in-out;
}
.burger::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 2px;
  background-color: var(--black-color);
  transition: transform 0.2s ease-in-out, top 0.2s ease-in-out;
}
.burger::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: var(--black-color);
  transition: transform 0.2s ease-in-out, bottom 0.2s ease-in-out;
}
.burger--active .burger__line {
  opacity: 0;
}
.burger--active::before {
  top: 50%;
  background-color: var(--black-color);
  transform: rotate(45deg);
}
.burger--active::after {
  top: 50%;
  bottom: auto;
  width: 100%;
  background-color: var(--black-color);
  transform: rotate(-45deg);
}

.header--fixed {
