.page__body.modal-open {
  position: relative;
  width: 100%;
  overflow: hidden;
  left: 0;
  right: 0;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  display: none;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal.active {
  display: flex;
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  cursor: pointer;
}

.modal__content {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: var(--mb-2) var(--mb-1);
  background: var(--white-color);
  max-width: 900px;
  width: 90%;
  max-height: 80vh;
  border-radius: var(--border-radius);
  overflow: hidden;
  z-index: 10;
}

.modal__title {
  margin-bottom: var(--mb-2);
  font-size: clamp(1.125rem, 0.982rem + 0.71vw, 1.625rem);
  font-weight: var(--semibold);
  line-height: var(--line-height);
  text-transform: uppercase;
  text-align: center;
}

.modal__video-wrapper {
  position: relative;
  padding-top: 56.25%;
  /* 16:9 ratio */
  height: 0;
}

.modal__video-wrapper video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.modal__close {
  position: absolute;
  top: 10px;
  right: 10px;
  background: transparent;
  border: none;
  font-size: 2rem;
  color: var(--dark-color);
  cursor: pointer;
  z-index: 20;
}