.sponsored {
  background-color: #0d2e620d;


  &__headline {
    text-align: center;
  }

  &__subtitle {
    margin-bottom: var(--mb-1);
    text-align: center;
  }

  &__text {
    margin: 0 auto 50px;
    max-width: 1000px;
    text-align: center;
  }

  &__list {
    grid-template-columns: repeat(3, 1fr);
    column-gap: var(--mb-3);
    row-gap: var(--mb-5);
    margin-bottom: var(--mb-5);
  }

  &__item {
    display: flex;
    flex-direction: column;
    border: 1px solid var(--dark-blue-color);
    border-radius: var(--border-radius);
    padding-bottom: var(--mb-3);
    background-color: var(--white-color);

    &-head {
      position: relative;
      min-height: 620px;
      padding: var(--mb-5) var(--mb-1);
      color: var(--white-color);
      text-align: center;
      background-color: var(--dark-blue-color);
    }

    &--accent {
      transform: scale(1.05);
    }
  }

  &__title {
    margin-bottom: var(--mb-1);
    font-size: clamp(1.125rem, 1.018rem + 0.54vw, 1.5rem);
    font-weight: var(--semibold-font);
    min-height: 60px;
    line-height: var(--line-height);
  }

  &__descr {
    margin: 0 auto;
    max-width: 90%;

    &--accent {
      position: absolute;
      right: 0;
      top: 0;
      background-color: var(--red-color);
      color: #fff;
      font-size: 12px;
      font-weight: bold;
      padding: 20px 20px;
      clip-path: polygon(0 0, 100% 0, 100% 100%, 100% 100%, 0 0%);

      span {
        display: block;
        transform: rotate(39deg) translateY(-18px) translateX(5px);
      }
    }
  }

  &__price {
    font-size: clamp(1.25rem, 1.107rem + 0.71vw, 1.75rem);
    color: var(--red-color);
    line-height: var(--line-height);

    span {
      display: block;
      font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
      font-weight: var(--light-font);
      color: var(--white-color);
      opacity: .7;
    }
  }

  &__sublist {
    padding: var(--mb-3) var(--mb-1);

    &-item {
      align-items: flex-start;
      gap: var(--mb-0-5);

      &:not(:last-child) {
        margin-bottom: var(--mb-1);
      }

      span {
        font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
        line-height: var(--line-height);
      }
    }

    &-icon {
      width: 20px;
      height: 20px;
      stroke: var(--dark-blue-color);
      fill: transparent;
    }

    &-btn {
      display: flex;
      justify-content: center;
      width: 90%;
      margin: auto auto 0;
    }
  }

  &__img {
    margin: var(--mb-1) 0;
    height: 300px;
    background-color: #fff;
  }

  &__accordion {
    margin: 0 auto;
    max-width: 1000px;
  }

  &__campaing {
    &-data {
      margin-bottom: var(--mb-1);
      color: var(--dark-blue-color);
      line-height: var(--line-height);
    }

    &-company {
      margin-bottom: var(--mb-1);
      font-weight: var(--medium-font);
      color: var(--blue-color);
    }

    &-list {
      grid-template-columns: repeat(3, 1fr);
      gap: var(--mb-1);
      margin-bottom: var(--mb-1);
    }

    &-text {
      margin-bottom: var(--mb-1);
    }

    &-item {
      display: flex;
      align-items: center;
      gap: var(--mb-0-5);

      svg {
        width: 20px;
        height: 20px;
        fill: transparent;
        stroke: var(--gray-color);
      }

      span {
        font-size: 14px;
        color: var(--gray-color);
      }
    }

    &-result {
      margin-bottom: var(--mb-2);
      border: 1px solid #198754;
      border-radius: var(--border-radius);
      padding: var(--mb-2) var(--mb-1);
      background-color: #19875451;
    }

    &-title {
      margin-bottom: var(--mb-1);
      color: #198754;
    }

    &-text {
      &--accent {
        font-weight: var(--medium-font);
        color: #198754;
      }
    }

  }

  &__row {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-1);
  }

  &__col {
    padding: 0 var(--mb-1);

    &:not(:last-child) {
      border-right: 1px solid #cecece;
    }
  }

  .accordion__header {
    background-color: var(--dark-grey-color);
    color: var(--white-color);
  }

  .accordion__icon {
    border-right: 2px solid var(--white-color);
    border-bottom: 2px solid var(--white-color);
  }

  .accordion__body {
    border-radius: var(--border-radius);
    max-width: 98%;
    margin: auto;
    background-color: var(--white-color);
  }
}

.benefits {
  &__item {
    align-items: center;
    gap: var(--mb-0-5);

    &:not(:last-child) {
      margin-bottom: var(--mb-1);
    }

    svg {
      width: 30px;
      height: 30px;
      stroke: var(--dark-blue-color);
      fill: transparent;
    }
  }
}


@media screen and (max-width:900px) {
  .sponsored {
    &__list {
      grid-template-columns: repeat(2, 1fr);
    }

    &__row {
      grid-template-columns: 1fr;
    }

    &__col {
      padding: var(--mb-1) 0;

      &:not(:last-child) {
        border-right: none;
        border-bottom: 1px solid #cecece;
      }
    }

    &__item {
      &--accent {
        transform: scale(1);
      }
    }
  }
}


@media screen and (max-width:600px) {
  .sponsored {
    &__list {
      grid-template-columns: 1fr;
      row-gap: var(--mb-1);
    }
  }
}
