.why-us {

  &__headline {
    text-align: center;
  }

  &__subtitle {
    margin-bottom: var(--mb-1);
    text-align: center;
  }

  &__descr {
    margin: 0 auto;
    max-width: 1000px;
    text-align: center;
  }

  &__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: var(--mb-3);
    margin-bottom: var(--mb-5);
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--mb-1);
    border-bottom: 1px solid var(--red-color);
    padding-bottom: var(--mb-1);
    text-align: center;
  }

  &__icon {
    width: 80px;
    height: 80px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: transparent;
      stroke: var(--blue-color);
    }
  }

  &__title {
    font-size: clamp(1.25rem, 1.071rem + 0.89vw, 1.875rem);
    color: var(--dark-blue-color);
    line-height: var(--line-height);
    font-variant-numeric: tabular-nums;
  }
}

