.circulation {

  &__title {
    text-align: center;
  }

  &__text {
    margin: 0 auto 50px;
    max-width: 1000px;
    text-align: center;
  }


  &__list {
    grid-template-columns: repeat(5, 1fr);
    gap: var(--mb-1);
    margin-top: var(--mb-5);
    margin-bottom: var(--mb-1);
  }

  &__item {
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--mb-1);
    border: 1px solid var(--dark-blue-color);
    border-radius: var(--border-radius);
    padding: 50px 15px;
    text-align: center;
    background-color: var(--white-color);

    &-title {
      font-size: clamp(1.125rem, 1rem + 0.56vw, 1.5rem);
      font-weight: var(--semibold-font);
      line-height: var(--line-height);
    }

    &-number {
      font-size: clamp(1.25rem, 1.071rem + 0.89vw, 1.875rem);
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 60px;
      height: 60px;
      background-color: #000;
      clip-path: polygon(0 0, 100% 0, 100% 20%, 20% 100%, 0 100%);
    }

    &:nth-child(1) {
      border-color: #0d6efd;

      &::before {
        background-color: #0d6efd;
      }
    }

    &:nth-child(2) {
      border-color: #0d2e62;

      &::before {
        background-color: #0d2e62;
      }
    }

    &:nth-child(3) {
      border-color: #343a40;

      &::before {
        background-color: #343a40;
      }
    }

    &:nth-child(4) {
      border-color: #dc3545;

      &::before {
        background-color: #dc3545;
      }
    }

    &:nth-child(5) {
      border-color: #198754;

      &::before {
        background-color: #198754;
      }
    }
  }

  &__sublist {
    grid-template-columns: repeat(3, 1fr);
    gap: var(--mb-1);
    margin-bottom: var(--mb-3);

    &-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: var(--mb-1);
      text-align: center;
    }

    &-icon {
      width: 80px;
      height: 80px;
      fill: transparent;
      stroke: var(--blue-color);
      svg {
        width: 100%;
        height: 100%;
      }
    }
    &-title {
      font-size: clamp(1.125rem, 0.982rem + 0.71vw, 1.625rem);
      font-weight: var(--semibold-font);
      line-height: var(--line-height);
    }
  }
}

@media screen and (max-width:1000px) {
  .circulation {
    &__list {
      grid-template-columns: repeat(3, 1fr);
    }
  }
}

@media screen and (max-width:600px) {
  .circulation {
    &__sublist {
      grid-template-columns: 1fr;
    }
    &__list {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}

@media screen and (max-width:400px) {
  .circulation {
    &__list {
      grid-template-columns: 1fr;
    }
  }
}
