html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
p,
ul,
ol {
  margin: 0;
  padding: 0;
}

.page__body {
  position: relative;
  overflow-x: hidden;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-family: var(--font-family);
  font-weight: var(--regular-font);
  font-size: 16px;

}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  min-height: 100vh;
}

.main {
  position: relative;
  flex: 1 1 auto;
}

.is-hidden {
  display: none !important;
}

.dis-scroll {
  position: fixed;
  overflow: hidden;
  width: 100%;
}

.no-scroll {
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
}

.centered {
  text-align: center;
}

.page--ios .dis-scroll {
  position: relative;
}


.section {
  padding: 80px 0;

  &__title {
    margin-bottom: var(--mb-3);
    font-size: clamp(1.625rem, 1.196rem + 2.14vw, 3.125rem);
    line-height: var(--line-height);
  }

  &__subtitle {
    font-size: clamp(1.375rem, 1.232rem + 0.71vw, 1.875rem);
    font-weight: var(--semibold-font);
    line-height: var(--line-height);
    text-transform: capitalize;
  }

  &__text {
    font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
    line-height: var(--line-height);
  }

  &__btn {
    position: relative;
    display: inline-flex;
    align-items: center;
    gap: 10px;
    overflow: hidden;
    border-radius: var(--border-radius);
    padding: var(--mb-1) var(--mb-2);
    font-weight: var(--semibold-font);
    color: var(--blue-color);
    background-color: var(--orange-color);
    transition: background-color 0.2s ease-in-out;

    span {
      position: relative;
      z-index: 2;
      transition: .2s ease-in-out;
    }

    svg {
      position: relative;
      z-index: 2;
      width: 24px;
      height: 24px;
      fill: var(--white-color);
      transition: .2s ease-in-out;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      border-radius: var(--border-radius);
      width: 100%;
      height: 100%;
      background-color: var(--blue-color);
      transform: translateX(-200%);
      transition: transform .2s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      &::before {
        transform: translateX(0);
      }

      span {
        color: var(--orange-color);
      }

      svg {
        transform: translateX(5px);
      }

    }

    &:active {
      background-color: var(--dark-grey-color);

      &::before {
        transform: translateX(-200%);
      }
    }

    &--secondary {
      background-color: var(--blue-color);

      span {
        color: var(--orange-color);
      }

      &::before {
        background-color: var(--orange-color);

      }

      &:focus {
        outline: none;
      }

      @include hover {
        &::before {
          transform: translateX(0);
        }

        span {
          color: var(--blue-color);
        }

      }

      &:active {
        background-color: var(--dark-grey-color);

        &::before {
          transform: translateX(-200%);
        }
      }
    }
  }
}


.swiper-pagination-bullet {
  width: 15px;
  height: 15px;
  background-color: var(--dark-blue-color);
  transition: all .3s ease-in-out;
}

.swiper-pagination-bullet-active {
  width: 35px !important;
  background-color: var(--orange-color);
  border-radius: var(--border-radius);
}


@media screen and (max-width:768px) {
  .section {
    padding: 50px 0;
  }
}

