:root {
  // base
  --font-family: "Montserrat", sans-serif;
  --content-width: 1470px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // fonts
  --light-font: 300;
  --regular-font: 400;
  --medium-font: 500;
  --semibold-font: 600;
  --bold-font: 700;


  // colors
  --white-color: #ffffff;
  --light-color: #f6f9fe;
  --black-color: #000000;
  --dark-color: #212529;
  --blue-color: #003b7b;
  --dark-blue-color: #0d2e62;
  --gray-color: #6c757d;
  --dark-grey-color: #343a40;
  --red-color: #dc3545;
  --orange-color:#fbb040;
  --green-color: #198754;

  // offsets
  --mb-0-5: 5px;
  --mb-0-75: 10px;
  --mb-1: 15px;
  --mb-2: 20px;
  --mb-3: 30px;
  --mb-4: 40px;
  --mb-5: 50px;


  // others
  --line-height: 130%;
  --border-radius: 5px;

}


