.podcast {
  background-color: #0d2e620d;

  &__container {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-3);
    margin-bottom: var(--mb-3);
  }

  &__title {
    margin-bottom: var(--mb-1);

    &--center {
      text-align: center;
    }
  }

  &__subtitle {
    margin-bottom: var(--mb-1);
    font-size: clamp(1.125rem, 1.054rem + 0.36vw, 1.375rem);
  }

  &__text {
    margin-bottom: var(--mb-1);

    &--center {
      margin: 0 auto 50px;
      max-width: 1000px;
      text-align: center;
    }
  }

  &__list {
    grid-template-columns: repeat(3, 1fr);
    gap: var(--mb-1);
    margin-bottom: var(--mb-3);

    &-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: var(--mb-1);
      text-align: center;
    }

    &-title {
      font-size: clamp(1.25rem, 1.071rem + 0.89vw, 1.875rem);
      color: var(--dark-blue-color);
      line-height: var(--line-height);
      font-variant-numeric: tabular-nums;
    }
  }



  &__audio {
    margin-bottom: var(--mb-1);

    audio {
      width: 100%;
    }
  }
}

.podcast-sublist {
  grid-template-columns: repeat(3, 1fr);
  column-gap: var(--mb-1);
  row-gap: var(--mb-3);

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--mb-0-75);
    text-align: center;
  }

  &__icon {
    width: 60px;
    height: 60px;

    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  &__title {
    font-size: clamp(1.125rem, 1.089rem + 0.18vw, 1.25rem);
    font-weight: var(--medium-font);
    line-height: var(--line-height);
  }

  &__text {
    font-size: 16px;
    &--highlight {
      border: 1px solid var(--dark-blue-color);
      border-radius: 50px;
      padding: var(--mb-0-5) var(--mb-2);
      font-weight: var(--medium-font);
      background-color: var(--light-color);
    }
  }
}

@media screen and (max-width:1000px) {
  .podcast {
    &__container {
      display: flex;
      flex-direction: column;
    }
    &-sublist {
      grid-template-columns: repeat(3, 1fr);
    }
  }
}

@media screen and (max-width:767px) {
  .podcast {
    &-sublist {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}

@media screen and (max-width:500px) {
  .podcast {
    &-sublist {
      grid-template-columns: 1fr;
    }
  }
}
