.about {
  &__container {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-1);
    margin-bottom: 50px;
  }

  &__title {
    margin-bottom: var(--mb-4);
    text-align: center;
  }

  &__text {
    margin-bottom: var(--mb-1);

    &--center {
      margin: 0 auto var(--mb-3);
      max-width: 1000px;
      font-weight: var(--medium-font);
      text-align: center;
    }
  }

  &__subtitle {
    margin-bottom: var(--mb-5);
    text-align: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: var(--mb-2);
    column-gap: var(--mb-1);
    margin-bottom: var(--mb-2);
    max-width: 600px;

    &-title {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      gap: 5px;
      margin-bottom: var(--mb-0-75);
      font-size: clamp(1.25rem, 1.071rem + 0.89vw, 1.875rem);
      color: var(--dark-blue-color);
      line-height: var(--line-height);
      font-variant-numeric: tabular-nums;
    }

    &-subtitle {
      margin-bottom: 5px;

      font-weight: var(--semibold-font);
    }

    &-text {
      color: var(--gray-color);
      line-height: var(--line-height);
    }
  }
}

.about__accordion {
  .accordion__header {
    background-color: var(--light-color);
  }
}

.portfolio {
  &__list {
    grid-template-columns: repeat(4, 1fr);
    column-gap: var(--mb-1);
    row-gap: 30px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: var(--mb-1);
    text-align: center;
  }

  &__title {
    font-size: clamp(1.25rem, 1.214rem + 0.18vw, 1.375rem);
    font-weight: var(--semibold-font);
    line-height: var(--line-height);
  }

  &__text {
    line-height: var(--line-height);
  }

  &__icon {
    width: 50px;
    height: 50px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: var(--blue-color);
    }
  }
}

.chart__title {
  margin-bottom: var(--mb-3);
  font-size: clamp(1.625rem, 1.196rem + 2.14vw, 3.125rem);
  text-align: center;
  line-height: var(--line-height);

  span {
    color: var(--red-color);
    text-decoration: underline;
  }
}

.number {
  display: inline-block;

}

.plus {
  display: inline-block;
}

@media screen and (max-width:1024px) {
  .about {
    &__container {
      grid-template-columns: 1fr;
      gap: var(--mb-3);
    }

    &__image {
      margin: auto;
      flex: 1 1 100%;
      width: 70%;
    }

    &__info {
      flex: 1 1 100%;
    }
  }
}


@media screen and (max-width:900px) {
  .portfolio {
    &__list {
      grid-template-columns: repeat(3, 1fr);
    }
  }
}


@media screen and (max-width:600px) {
  .about {
    &__image {
      width: 100%;
    }
  }

  .portfolio {
    &__list {
      grid-template-columns: repeat(2, 1fr);
    }

    &__icon {
      width: 60px;
      height: 60px;
    }
  }
}

@media screen and (max-width:375px) {
  .about {
    &__list {
      grid-template-columns: 1fr;
    }
  }
}
