.jobs {
  &__container {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-3);
  }

  &__text {
    margin-bottom: var(--mb-1);
  }

  &__subtitle {
    margin-bottom: var(--mb-1);
  }

  &__list {
    margin-bottom: var(--mb-1);
    padding-left: var(--mb-3);
    list-style: square;

    &:last-of-type {
      margin-bottom: var(--mb-3);
    }

    &-item {
      &:not(:last-child) {
        margin-bottom: var(--mb-1);
      }
    }
  }
}

@media screen and (max-width:1000px) {
  .jobs {
    &__container {
      grid-template-columns: 1fr;
    }
  }
}
