.footer {
  padding: 50px 0;

  background-color: var(--dark-color);

  &__row {
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
    border-bottom: 1px solid var(--white-color);
    margin-bottom: var(--mb-3);
    padding-bottom: var(--mb-3);

    @media (max-width: 1000px) {
      grid-template-columns: 1fr;
    }
  }

  &__title {
    font-size: clamp(1.125rem, 1.089rem + 0.18vw, 1.25rem);
    font-weight: var(--semibold-font);
    margin-bottom: var(--mb-1);
    color: var(--white-color);
    line-height: var(--line-height);
  }

  &__copy {
    font-size: 14px;
    color: var(--white-color);

    a {
      color: var(--white-color);
      text-decoration: underline;
      transition: color .2s ease-in-out;

      &:hover {
        color: var(--red-color);
      }
    }
  }

  &__list {
    li {
      &:not(:last-child) {
        margin-bottom: var(--mb-1);
      }
    }
  }

  &__contact {
    li {
      &:not(:last-child) {
        margin-bottom: var(--mb-1);
      }
    }
  }

  &__social {
    gap: var(--mb-0-75);

    svg {
      width: 30px;
      height: 30px;
      fill: #fff;
      transition: fill .2s ease-in-out;

      &:hover {
        fill: var(--red-color);
      }
    }
  }

  &__link {
    color: var(--white-color);
    line-height: var(--line-height);
    transition: color .2s ease-in-out;

    &:hover {
      color: var(--red-color);
    }
  }
}

.footer__accordion-btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  text-align: left;
}


.dropdown-arrow {
  display: none;
  width: 12px;
  height: 12px;
  border-right: 2px solid var(--white-color);
  border-bottom: 2px solid var(--white-color);
  transform: rotate(45deg);
  transition: transform 0.3s ease;
}

.footer__accordion.is-active .dropdown-arrow {
  transform: rotate(225deg);
}

.footer__list,
.footer__contact {
  transition: max-height 0.3s ease, opacity 0.3s ease;
  overflow: hidden;
}

@media (max-width: 1000px) {
  .footer__row {
    grid-template-columns: 1fr;
  }

  .dropdown-arrow {
    display: block;
  }

  .footer__title {
    margin-bottom: 0;
  }

  .footer__list,
  .footer__contact {
    max-height: 0;
    opacity: 0;
    visibility: hidden;
  }

  .footer__accordion.is-active .footer__list,
  .footer__accordion.is-active .footer__contact {
    margin-top: var(--mb-1);
    max-height: 500px;
    opacity: 1;
    visibility: visible;
  }
}

@media (min-width: 1000px) {
  .footer__accordion-btn {
    cursor: default;
    pointer-events: none;
  }

  .footer {
    &__list {
      margin-top: 15px;
    }
  }
}
