.content-marketing {
  background-color: #0d2e620d;


  &__headline {
    border-left: none;
    padding-left: 0;
    text-align: center;
  }

  &__text {
    margin: 0 auto 50px;
    max-width: 1000px;
    text-align: center;
  }
}

.marketing {

  &__row {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-1);
  }

  &__item {
    border: 1px solid #acccf5;
    border-radius: var(--border-radius);
    padding: var(--mb-5) var(--mb-1);
    background-color: #eff6ff;
    transition: all .3s ease-in-out;

    &:nth-child(2) {
      border-color: #34b378;
      background-color: #ecfdf5;

      .marketing__blockquote {
        border-color: #34b378;
        background-color: #ecfdf5;
      }

      svg {
        stroke: #34b378;
      }
    }

    &:nth-child(3) {
      border-color: #9333ea;
      background-color: #faf5ff;

      .marketing__blockquote {
        border-color: #9333ea;
        background-color: #faf5ff;
      }

      svg {
        stroke: #9333ea;
      }
    }

    &:nth-child(4) {
      border-color: #ea580b;
      background-color: #fff7ed;

      .marketing__blockquote {
        border-color: #ea580b;
        background-color: #fff7ed;
      }

      svg {
        stroke: #ea580b;
      }
    }

    &:nth-child(5) {
      border-color: #ea0b0b;
      background-color: #fce9e9;

      .marketing__blockquote {
        border-color: #ea0b0b;
        background-color: #fce9e9;
      }

      svg {
        stroke: #ea0b0b;
      }
    }
  }

  &__head {
    align-items: center;
    gap: var(--mb-1);
    margin-bottom: var(--mb-3);

    &-icon {
      width: 60px;
      height: 60px;

      svg {
        width: 100%;
        height: 100%;
        stroke: var(--blue-color);
        fill: transparent;
      }
    }

    &-title {
      margin-bottom: var(--mb-1);
      font-size: clamp(1.25rem, 1.179rem + 0.36vw, 1.5rem);
      font-weight: var(--semibold-font);
      line-height: var(--line-height);
    }

  }

  &__title {
    margin-bottom: var(--mb-1);
    font-size: clamp(1.125rem, 1.054rem + 0.36vw, 1.375rem);
    font-weight: var(--medium-font);
    line-height: var(--line-height);
  }

  &__list {
    &-item {
      gap: var(--mb-0-75);
      border-radius: var(--border-radius);
      padding: var(--mb-2) var(--mb-0-75);
      background-color: var(--white-color);

      &:not(:last-child) {
        margin-bottom: var(--mb-1);
      }
    }

    &-icon {
      width: 30px;
      height: 30px;

      svg {
        width: 100%;
        height: 100%;
        fill: transparent;
        stroke: var(--blue-color);
      }
    }

    &-title {
      margin-bottom: var(--mb-1);
      font-size: clamp(1rem, 0.929rem + 0.36vw, 1.25rem);
      font-weight: var(--semibold-font);
      line-height: var(--line-height);
    }

    &-text {
      &--accent {
        font-weight: var(--medium-font);
        color: var(--blue-color);
      }

      &:not(:last-child) {
        margin-bottom: var(--mb-1);
      }
    }
  }

  &__sublist {
    list-style: square;
    padding-left: var(--mb-3);

    &-item {
      &:not(:last-child) {
        margin-bottom: var(--mb-1);
      }
    }
  }

  &__col {
    &:last-of-type {
      border-radius: var(--border-radius);
      padding: var(--mb-2) var(--mb-1);

      background-color: var(--white-color);
    }
  }

  &__blockquote {
    margin: 0;
    margin-bottom: var(--mb-3);
    padding: var(--mb-2) var(--mb-1);
    border: 1px solid var(--dark-blue-color);
    border-radius: var(--border-radius);
    background-color: #eff6ff;

    &-title {
      margin-bottom: var(--mb-1);
      font-size: clamp(1.125rem, 1.018rem + 0.54vw, 1.5rem);
    }
  }
}

.email-marketing {
  background-color: #0d2e620d;

  &__row {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-3);
  }
  &__text {
    margin-bottom: var(--mb-1);
    &:last-of-type {
      margin-bottom: var(--mb-3);
    }
  }

  &__list {
    list-style: square;
    margin-bottom: var(--mb-1);
    padding-left: var(--mb-2);
  }

  &__item {
    strong {
      font-weight: var(--semibold-font);
    }
    &:not(:last-child) {
      margin-bottom: var(--mb-1);
    }
  }
}

@media screen and (max-width:1000px) {
  .email-marketing {
    &__row {
      grid-template-columns: 1fr;
    }
  }
}

@media screen and (max-width:767px) {
  .marketing {
    &__row {
      grid-template-columns: 1fr;
    }
  }
}
