.social-reach {
  &__headline {
    border-left: none;
    padding-left: 0;
    text-align: center;
  }

  &__text {
    margin-bottom: 50px;
    text-align: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    gap: var(--mb-1);
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    text-align: center;
    position: relative;
    overflow: hidden;
    min-height: 240px;


    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      height: 2px;
      width: 100%;
      background-color: var(--red-color);
      transform: translateX(-200%);
      transition: transform 0.3s ease-in-out;
    }

    &:hover::before {
      transform: translateX(0);
    }
  }

  &__icon {
    width: 80px;
    height: 80px;
    margin-bottom: var(--mb-1);
    flex-shrink: 0;
  }

  &__title {
    margin-bottom: var(--mb-1);
    font-size: clamp(1.625rem, 1.196rem + 2.14vw, 3.125rem);
    color: var(--dark-blue-color);
    line-height: var(--line-height);
  }

  &__caption {
    margin-bottom: 0;
    color: var(--dark-color);
    line-height: var(--line-height);
    text-align: center;
  }
}

@media screen and (max-width:500px) {
  .social-reach {
    &__item {
      min-height: 220px;
    }
  }
}
