.audience {
  background-color: #0d2e620d;

  &__container {
    gap: var(--mb-3);
  }

  &__chart {
    width: 60%;
    aspect-ratio: 1 / 1;
  }

  &__info {
    width: 40%;
  }

  &__title {
    padding-left: 0;
    border-left: 0;
    font-size: clamp(1.5rem, 1.214rem + 1.43vw, 2.5rem);

    span {
      color: var(--red-color);
      text-decoration: underline;
    }
  }

  &__text {
    margin-bottom: var(--mb-1);
  }

  &__subtitle {
    margin-bottom: var(--mb-3);
    font-size: clamp(1.375rem, 1.232rem + 0.71vw, 1.875rem);
    text-align: center;
  }

  &__list {
    grid-template-columns: repeat(3, 1fr);
    gap: var(--mb-1);
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--mb-0-5);
    border-bottom: 1px solid var(--red-color);
    height: 100%;
    padding-bottom: 10px;
    text-align: center;

    &-icon {
      width: 50px;
      height: 50px;

      svg {
        display: block;
        width: 100%;
        height: 100%;
        fill: transparent;
        stroke: var(--blue-color);
      }
    }

    &-title {
      min-height: 40px;
      font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
      font-weight: var(--medium-font);
    }

    &-number {
      font-size: clamp(1.25rem, 1.071rem + 0.89vw, 1.875rem);
    }

    &-text {
      font-size: clamp(0.875rem, 0.839rem + 0.18vw, 1rem);
      line-height: var(--line-height);
    }
  }
}

#audienceMap {
  width: 100%;
  height: 300px;
}

#audienceChart {
  width: 100%;
  height: 100%;
}


.chart-legend {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-family: sans-serif;
  font-size: 14px;
}

@media (max-width:1000px) {
  .audience {
    &__chart {
      width: 50%;
    }

    &__info {
      width: 50%;
    }
  }
}

@media (max-width: 800px) {
  .chart-wrapper {
    flex-direction: column;
  }

  .chart__title span {
    display: block;
  }

  .audience {
    &__container {
      flex-direction: column;
    }

    &__info,
    &__chart {
      width: 100%;
    }

    &__list {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  #audienceChart {
    width: 100% !important;
    height: 100% !important;
  }
}

canvas {
  display: block;
}

.label {
  position: absolute;
  color: #fff;
  background: rgba(13, 110, 253, 0.85);
  padding: 5px 10px;
  border-radius: 6px;
  font-size: 13px;
  pointer-events: none;
  transform: translate(-50%, -50%);
  white-space: nowrap;
}
