.tv {

  &__container {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-3);
    margin-bottom: var(--mb-3);
  }

  &__video {
    video {
      display: block;
      margin-bottom: var(--mb-3);
      width: 100%;
      height: 400px;
    }
  }

  &__list {
    grid-template-columns: repeat(3, 1fr);
    column-gap: var(--mb-1);
    row-gap: var(--mb-2);
    margin-bottom: var(--mb-3);

    &-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: var(--mb-1);
      text-align: center;
    }

    &-title {
      font-size: clamp(1.25rem, 1.071rem + 0.89vw, 1.875rem);
      color: var(--dark-blue-color);
      line-height: var(--line-height);
      font-variant-numeric: tabular-nums;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
  }

  &__text {
    margin-bottom: var(--mb-1);

    &--center {
      margin: 0 auto 50px;
      max-width: 1000px;
      text-align: center;
    }
  }

  &__features {
    justify-content: center;
    gap: var(--mb-1);
    margin-bottom: var(--mb-3);

    &-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      border-bottom: 1px solid var(--gray-color);
      padding-bottom: var(--mb-0-75);
    }

    &-title {
      margin-bottom: var(--mb-1);
      font-size: clamp(1.125rem, 1.089rem + 0.18vw, 1.25rem);
      line-height: var(--line-height);
      text-align: center;
    }

    &-text {
      text-align: center;
    }
  }

  &__item {
    justify-content: space-between;
    gap: var(--mb-1);
    border-bottom: 1px solid #cecece;
    padding: var(--mb-2) 0;

    &-name {
      width: 70%;
      font-size: clamp(1rem, 0.929rem + 0.36vw, 1.25rem);
      font-weight: var(--semibold-font);
    }

    &-price {
      width: 30%;
      font-size: clamp(1rem, 0.929rem + 0.36vw, 1.25rem);
      font-weight: var(--semibold-font);
      color: var(--red-color);
      text-align: right;
    }

  }
}

@media screen and (max-width:1000px) {
  .tv {
    &__container {
      display: flex;
      flex-direction: column-reverse;
    }
    &__features {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}

@media screen and (max-width:768px) {
  .tv {
    &__video {
      video {
        height: 100%;
      }
    }

    &__item {
      &-name {
        width: 60%;
      }

      &-price {
        width: 40%;
      }
    }

    &__list {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}


@media screen and (max-width:500px) {
  .tv {
    &__features {
      flex-direction: column;
    }
  }
}
